import styled, { keyframes } from "styled-components";

import { colors } from "../styleguide";

import { SpinnerSizes, StyledSpinnerProps } from "./Spinner.types";

export const spinnerSize: Record<SpinnerSizes, string> = {
	Small: "40px",
	Medium: "72px",
	Large: "96px",
	XLarge: "160px",
};

const loadingAnimationStrokeWidth = keyframes`
    0% {
        stroke-width: 10;
    }

    50% {
        stroke-width: 1;
    }

    100% {
        stroke-width: 3;
    }
`;

const loadingAnimationStrokeDash = keyframes`
    0% {
      stroke-dasharray: 1, 200;
      stroke-dashoffset: 0;
    }

    50% {
      stroke-dasharray: 89, 200;
      stroke-dashoffset: -35px;
    }

    100% {
      stroke-dasharray: 89, 200;
      stroke-dashoffset: -124px;
    }
`;

const loadingAnimationStrokeColor = keyframes`
	0%,
	100% {
        stroke: ${colors.midBlue};
    }
`;

const loadingAnimationRotate = keyframes`
    100% {
      transform: rotate(360deg);
    }
`;

export const StyledSpinner = styled.div<StyledSpinnerProps>`
    position: relative;
    pointer-events: none;
    width: ${({ size }) => size && spinnerSize[size]};
    height: ${({ size }) => size && spinnerSize[size]};
`;

export const Circle = styled.circle`
    stroke-width: 3;
    stroke-linecap: round;
	animation: ${loadingAnimationStrokeWidth} .5s ease-in-out,
		${loadingAnimationStrokeDash} 1.5s ease-in-out infinite,
		${loadingAnimationStrokeColor} 6s ease-in-out infinite;
`;

export const Svg = styled.svg`
    animation: ${loadingAnimationRotate} 2s linear infinite;
`;

